//navbar.scss
.navibar{
    width: 100%;
    min-height: 7vh;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffe484;
    transition: all .4s ease-in;
  }
  //IMPORTANT
  .scrolled{
    position: fixed;
    top: 0;
    left: 0;
    background-color:#ffe499;
    min-height: 9vh;
    z-index: 999
  }
  .logo img{
    width: 50px;
  }
