
.filter-main{
  text-align: left;
  li{
    list-style: none;
    cursor: pointer;
    &.selected{
      font-weight: 600;
    }
  }
  border-right: 2px solid #ddd;
  padding-right: 12px;
  line-height: 16px;
  word-break: break-word;
}
.filter-mobile{

    .selected{
      font-weight: 600;
    }
    display: flex;
    justify-content: space-around;
    input{
      width: 50px;
      margin: 0 5px;

    }
}
.custom-filters{
  margin-top: 0;
  margin-bottom: 1rem;
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 40px;
    input{
      width: 55px;
      margin: 0 5px;
    }
}